import './style.css';

export default class AppView {
  constructor() {
    this.firstPageInPagingFrame = 1;
    this.onClickEnterInSearch = null;
    this.switchToNextPage = null;
    this.swipeStart = null;
    this.swipeEnd = null;
    this.openVideo = null;
  }

  pageLoad() {
    const fragment = document.createDocumentFragment();
    const searchContainer = document.createElement('div');
    searchContainer.className = 'search-container';
    const searchBar = document.createElement('input');
    searchBar.setAttribute('type', 'text');
    searchBar.setAttribute('id', 'input-text');
    searchBar.className = 'search-input';
    searchBar.placeholder = 'Search';
    searchBar.onkeyup = this.onClickEnterInSearch;
    searchContainer.appendChild(searchBar);
    fragment.appendChild(searchContainer);

    const videosContainer = document.createElement('div');
    videosContainer.className = 'videos-container';
    videosContainer.id = 'videos';
    fragment.appendChild(videosContainer);

    const dotsContainer = document.createElement('div');
    dotsContainer.id = 'dots';
    fragment.appendChild(dotsContainer);

    document.body.appendChild(fragment);
  }

  render(clips, currentPage, pageCount) {
    function openVideo(id) {
      window.open(`https://www.youtube.com/watch?v=${id}`, '_blank');
    }
    const videosSection = document.getElementById('videos');
    videosSection.style.setProperty('--i', '0');
    videosSection.innerHTML = '';

    for (let i = 0; i < clips.length; i += 1) {
      const videoContainer = document.createElement('div');
      videoContainer.className = 'video-section';
      videoContainer.style.height = '100%';
      const fragment = document.createDocumentFragment();

      const videoImg = document.createElement('img');
      videoImg.setAttribute('src', `${clips[i].thumbnails}`);
      videoImg.setAttribute('alt', 'video thumbnail');
      videoImg.className = 'video-img';
      fragment.appendChild(videoImg);

      const titleContainer = document.createElement('div');
      titleContainer.className = 'video-title';
      titleContainer.addEventListener('mousedown', () => { openVideo(clips[i].id); });
      titleContainer.addEventListener('touchend', () => { openVideo(clips[i].id); });
      titleContainer.innerHTML = `<p>${clips[i].title}</p>`;
      fragment.appendChild(titleContainer);

      const authorContainer = document.createElement('div');
      authorContainer.className = 'video-author';
      authorContainer.innerHTML = `<p>${clips[i].author}</p>`;
      fragment.appendChild(authorContainer);

      const publishedAtContainer = document.createElement('div');
      publishedAtContainer.className = 'video-publishedAt';
      publishedAtContainer.innerHTML = `<p>${clips[i].publishedAt}</p>`;
      fragment.appendChild(publishedAtContainer);

      const rateContainer = document.createElement('div');
      rateContainer.className = 'video-rate';
      rateContainer.innerHTML = `<p>Views: ${clips[i].rate}</p>`;
      fragment.appendChild(rateContainer);

      const descriptionContainer = document.createElement('div');
      descriptionContainer.className = 'video-description';
      descriptionContainer.innerHTML = `<p>${clips[i].description}</p>`;
      fragment.appendChild(descriptionContainer);

      videoContainer.appendChild(fragment);
      videosSection.appendChild(videoContainer);
    }

    const dotContainer = document.getElementById('dots');
    dotContainer.innerHTML = '';

    let spanDot = document.createElement('span');
    spanDot.className = 'dot';
    spanDot.innerHTML = '<span>&#60</span>';
    if (currentPage === 1) {
      spanDot.style.display = 'none';
    } else {
      spanDot.onclick = this.switchToNextPage;
    }
    dotContainer.appendChild(spanDot);

    let firstPage = 1;
    if (currentPage > pageCount + this.firstPageInPagingFrame - 1) {
      this.firstPageInPagingFrame += 1;
      firstPage = this.firstPageInPagingFrame;
    }
    if (currentPage < this.firstPageInPagingFrame) {
      this.firstPageInPagingFrame = currentPage;
    }
    if (currentPage < this.firstPageInPagingFrame + pageCount) {
      firstPage = this.firstPageInPagingFrame;
    }
    for (let i = firstPage; i < firstPage + pageCount; i += 1) {
      spanDot = document.createElement('span');
      spanDot.className = 'dot';
      if (i === currentPage) {
        spanDot.className = 'dot dot-active';
      }
      spanDot.innerHTML = `<span>${i}</span>`;

      spanDot.onclick = this.switchToNextPage;
      dotContainer.appendChild(spanDot);
    }
    spanDot = document.createElement('span');
    spanDot.className = 'dot';
    spanDot.innerHTML = '<span>&#62</span>';
    spanDot.onclick = this.switchToNextPage;
    dotContainer.appendChild(spanDot);


    document.getElementById('videos').addEventListener('mousedown', this.swipeStart);
    document.getElementById('videos').addEventListener('touchstart', this.swipeStart);

    document.getElementById('videos').addEventListener('mousedown', (e) => { e.preventDefault(); });
    document.getElementById('videos').addEventListener('touchstart', (e) => { e.preventDefault(); });

    document.getElementById('videos').addEventListener('mouseup', this.swipeEnd);
    document.getElementById('videos').addEventListener('touchend', this.swipeEnd);
  }
}
