export default class AppModel {
  constructor() {
    this.XMLHttpRequest = XMLHttpRequest;
    this.clips = [];
    this.nextPageToken = '';
  }

  async getClips(searchStr, fn, videoPerPage, currentPage) {
    let pageToken = '';
    if ((this.clips.length - videoPerPage * currentPage) < 5) {
      pageToken = this.nextPageToken;
    }
    if (searchStr !== '' || pageToken !== '') {
      const request = new this.XMLHttpRequest();
      request.open(
        'GET',
        `https://www.googleapis.com/youtube/v3/search?pageToken=${pageToken}&key=AIzaSyAg2cyvC_oXnbezqFyubT_xNFz0BUJZQRU&type=video&part=snippet&maxResults=15&q=${searchStr}`,
        false,
      );
      await request.send();
      if (request.status !== 200) {
        throw new Error(request.status.concat(request.statusText));
      } else {
        const newResponse = JSON.parse(request.responseText);
        if (pageToken !== '') {
          this.clips = this.clips.concat(newResponse.items);
          this.nextPageToken = newResponse.nextPageToken;
        } else {
          this.clips = newResponse.items;
          this.nextPageToken = newResponse.nextPageToken;
        }
      }
    }
    const resp = this.clips.slice((currentPage - 1) * videoPerPage, currentPage * videoPerPage);
    fn(resp);
  }

  getViewsCount(id) {
    const request = new this.XMLHttpRequest();
    request.open('GET', `https://www.googleapis.com/youtube/v3/videos?id=${id}&key=AIzaSyDWZ7h4sS6Z1YIrVy6kahJNvX-9WQdakvs&part=statistics`, false);
    request.send();
    if (request.status !== 200) {
      throw new Error(request.status.concat(request.statusText));
    } else {
      return JSON.parse(request.responseText);
    }
  }
}
