import AppModel from '../models/AppModel';
import AppView from '../views';

export default class App {
  constructor() {
    this.appModel = new AppModel();
    this.appView = new AppView();
    this.videoPerPage = 4;
    this.currentPage = 1;
    this.numberOfPagingDots = 2;
    this.x0 = null;
  }

  async start() {
    this.appView.onClickEnterInSearch = this.onClickEnterInSearch.bind(this);
    this.renderClips = this.renderClips.bind(this);
    this.calculateWindowSize = this.calculateWindowSize.bind(this);
    this.redrawOnResize = this.redrawOnResize.bind(this);
    this.appView.swipeStart = this.swipeStart.bind(this);
    this.appView.swipeEnd = this.swipeEnd.bind(this);
    this.appView.switchToNextPage = this.switchToNextPage.bind(this);
    this.appView.pageLoad();
  }

  onClickEnterInSearch(e) {
    const str = document.getElementById('input-text').value;
    this.currentPage = 1;
    if (e.keyCode === 13) {
      this.appModel.getClips(str,
        this.renderClips.bind(this),
        this.videoPerPage,
        this.currentPage);
    }
  }

  async renderClips(response) {
    const Clips = [];
    let i = 0;
    response.forEach((element) => {
      Clips[i] = {};
      Clips[i].thumbnails = element.snippet.thumbnails.medium.url;
      Clips[i].title = element.snippet.title;
      Clips[i].description = element.snippet.description;
      Clips[i].publishedAt = element.snippet.publishedAt;
      Clips[i].author = element.snippet.channelTitle;
      Clips[i].id = element.id.videoId;
      Clips[i].rate = this.appModel.getViewsCount(Clips[i].id).items[0].statistics.viewCount;
      i += 1;
    });

    this.appView.render(Clips, this.currentPage, this.numberOfPagingDots);
  }

  switchToNextPage(e) {
    if (e.target.innerText === '>') {
      this.currentPage += 1;
    }
    if (e.target.innerText === '<') {
      this.currentPage -= 1;
    }
    if ((e.target.innerText !== '>') && (e.target.innerText !== '<')) {
      this.currentPage = +e.target.innerText;
    }
    this.appModel.getClips('', this.renderClips.bind(this), this.videoPerPage, this.currentPage);
  }

  swipeStart(e) {
    const event = e.changedTouches ? e.changedTouches[0] : e;
    this.x0 = event.clientX;
  }

  swipeEnd(e) {
    const event = e.changedTouches ? e.changedTouches[0] : e;
    if ((this.x0 - event.clientX) < 0) {
      if (this.currentPage !== 1) {
        document.getElementById('videos').style.setProperty('--i', '100%');
        this.currentPage -= 1;
        this.appModel.getClips('', this.renderClips.bind(this), this.videoPerPage, this.currentPage);
      }
    } else {
      document.getElementById('videos').style.setProperty('--i', '-100%');
      this.currentPage += 1;
      this.appModel.getClips('', this.renderClips.bind(this), this.videoPerPage, this.currentPage);
    }
    this.x0 = null;
  }

  calculateWindowSize() {
    const screenWidth = document.documentElement.clientWidth;
    this.videoPerPage = 4;
    this.numberOfPagingDots = 2;
    if (screenWidth < 1200) {
      this.videoPerPage = 3;
      this.numberOfPagingDots = 3;
    }
    if (screenWidth < 900) {
      this.videoPerPage = 2;
      this.numberOfPagingDots = 4;
    }
    if (screenWidth < 600) {
      this.videoPerPage = 1;
    }
  }

  async redrawOnResize() {
    const firstElementOnPage = (this.currentPage - 1) * this.videoPerPage + 1;
    this.calculateWindowSize();
    this.currentPage = Math.ceil(firstElementOnPage / this.videoPerPage);
    this.appModel.getClips('', this.renderClips.bind(this), this.videoPerPage, this.currentPage);
  }
}
